
import { Options, Vue } from "vue-class-component";

@Options({
    name: "ListOutput",
    props: {
        missing_cards: {
            type: Map,
            required: true,
        },
    },
    methods: {
        convert_map_to_string(map: Map<string, number>) {
            let output = "";
            map.forEach((quantity: number, name: string) => {
                output += `${quantity} ${name}\n`;
            });
            return output;
        },
        copy_to_clipboard() {
            const cards = this.decklist;
            navigator.clipboard.writeText(cards).then(
                () => {
                    console.log("copied to clipboard");
                },
                (err) => {
                    console.error("could not copy to clipboard", err);
                }
            );
        },
    },
    watch: {
        missing_cards: {
            immediate: true,
            handler() {
                this.decklist = this.convert_map_to_string(this.missing_cards);
            },
        },
    },
})
export default class ListOutput extends Vue {}
